<template>
  <section>
    <div class="btn-group">
      <button class="add-row-btn" @click="addDataRow">
        <font-awesome-icon :icon="['fa', 'plus']" class="font-awesome-icon" /><span>Add Row</span>
      </button>
      <button class="save-btn" @click="saveData">
        <font-awesome-icon 
          :icon="['far', 'floppy-disk']" 
          class="font-awesome-icon" />
          <span>Save</span>
      </button>
      <button class="preview-btn">
        <font-awesome-icon :icon="['fa', 'align-left']" class="font-awesome-icon" /><span>Preview</span>
      </button>
      <button class="cancel-btn" @click="cancel">
        <font-awesome-icon :icon="['fa', 'window-close']" class="font-awesome-icon" /><span>Cancel</span>
      </button>
    </div>
    <div>
      <div class="data-grid-head">
        <p>Data Type</p>
        <p>Data Key Name</p>
        <p>Data Options</p>
      </div>
      <div>
        <DataEditRow />
      </div>
    </div>
  </section>
</template>
<script>
import { defineComponent, defineAsyncComponent } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import router from "@/router";

export default defineComponent({
  components: {
    DataEditRow: defineAsyncComponent(() => import('@/components/data/DataEditRow.vue')),
  },
  props: {
    projectNamespace: String
  },
  setup(props) {
    const store = useStore();

    const addDataRow = () => {
      store.commit("addDataRow");
    }

    const saveData = async () => {
      store.commit('startLoading');
      await axios
        .put(
          'https://dev-faker-be.herokuapp.com/project/' + props.projectNamespace + '/data', 
          {
            dataType: store.getters.getDataType,
            dataRows: store.getters.getDataRows,
            array: store.getters.getArrayCheck,
            list: store.getters.getDataRowList,
            dataName: store.getters.getDataName,
            dataPath: store.getters.getDataPath
          },
          {
            headers: {
              accept: 'application/json',
              'Content-Type': 'application/json',
              'Access-Control-Allow-Origin': '*',
              Authorization: 'Bearer ' + localStorage.getItem("token"),
            },
          }
        )
        .then(res => {
          if (res.status === 200) {
            let data = res.data;
            console.log(data);
            // spinner 로딩 중지
            store.commit('stopLoading');
            store.commit('initDataInfo');

            router.push({name: 'ProjectList'});
          }
        })
        .catch(error => {
          store.commit('stopLoading');
          console.log('error ->', error.response);

          store.commit('initDataInfo');
        });
      
      store.commit('stopLoading');
    };

    const cancel = () => {
      store.commit("initDataInfo");
      router.push({name: 'ProjectList'});
    };

    return {
      addDataRow,
      saveData,
      cancel
    }
  }
});
</script>
